.promo {
  background-color: $light-gray;
  background-image: url("../img/bg-promo-desktop@1x.png");
  background-repeat: no-repeat;
  background-size: 1920px 1043px;
  background-position: 50% 0;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-promo-desktop@2x.png");
  }

  @media (max-width: $desktop-breakpoint) {
    background-size: cover;
  }

  @media (max-width: $mobile-breakpoint) {
    background-position: 50% 150px;
  }
}

.promo__container {
  padding: 208px 263px 613px;
  background-image: url("../img/bg-promo-device@1x.png");
  background-repeat: no-repeat;
  background-size: 892px 608px;
  background-position: 124px 452px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-promo-device@2x.png");
  }

  @media (max-width: $tablet-breakpoint) {
    padding: 120px 13.7% 291px;
    background-size: 537px auto;
    background-position: 50% 332px;
  }

  @media (max-width: $mobile-breakpoint) {
    padding: 95px 16px 180px;
    background-size: 288px auto;
    background-position: 50% 400px;
  }
}

.promo__title {
  margin: 0;
  margin-bottom: 34px;
  color: $dark-gray;
  font-size: 36px;
  line-height: 24px;
  font-weight: 700;
  text-align: center;

  @media (max-width: $tablet-breakpoint) {
    font-size: 20px;
    line-height: 17px;
  }
}

.promo__content {
  margin: 0;
  margin-bottom: 32px;
  color: $gray;
  font-size: 16px;
  line-height: 27px;
  text-align: center;

  @media (max-width: $tablet-breakpoint) {
    font-size: 14px;
    line-height: 24px;
  }
}

.promo__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (max-width: $mobile-breakpoint) {
    flex-direction: column;
  }
}

.promo__download {
  display: block;
  margin-right: 29px;
  padding: 12px 45px;
  color: $white;
  font-size: 16px;
  line-height: 27px;
  text-align: center;
  text-decoration: none;
  border: none;
  border-radius: 25px;
  background-color: $blue;
  outline: none;

  &:hover,
  &:focus {
    background-color: $hover-blue;
  }

  &:active {
    background-color: $hover-blue;
    opacity: 0.7;
  }

  @media (max-width: $mobile-breakpoint) {
    margin: 0 40px 10px 40px;
  }
}

.promo__free-trial {
  display: block;
  padding: 10px 45px;
  color: $blue;
  font-size: 16px;
  line-height: 27px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  border: 2px solid $blue;
  border-radius: 25px;
  background-color: $white;
  outline: none;

  &:hover,
  &:focus {
    border: 2px solid $hover-blue;
    color: $hover-blue;
  }

  &:active {
    color: $hover-blue;
    opacity: 0.7;
  }

  @media (max-width: $mobile-breakpoint) {
    margin: 0 40px;
  }
}
